<template>
    <div>
        <navbar title="转账"  v-if="$phonemodel == 'Android'"></navbar>
        <div class="app" :class="{nav:$phonemodel == 'Android'}">
            <div class="addr hadd">
                <p>转账账号</p>
                <input type="text" v-model="transferForm.username" placeholder="请输入对方账号">
            </div>
            <div class="addr pr">
                <p>转账数量</p>
                <input type="text"  v-model="transferForm.money" placeholder="请输入">
                <span>可用：{{walletData.money}}USDT</span>
            </div>
            <div class="btn"><button @click="transferSub">确定</button></div>
        </div>
    </div>
</template>

<script>
    import {wallet, transfer} from "../../api/center";
    import fmath from "../../utils/math"
    import { toast, Dialog } from 'vant';
    import navbar from "@/components/navbar";
    import {cashlist, transferlist} from "@/page/mine/components";
    import { validMoney } from "../../utils/validate";
    export default {
        name: "Transfer",
        components:{
          navbar
        },
        data()
        {
            return {
                walletData:{},
                transferForm:{
                    username:'',
                    money:''
                },
            }
        },
        mounted()
        {
            this.getWallet()
        },
        methods:{
            getWallet()
            {
                wallet().then(res => {
                    this.walletData = res
                })
            },

            transferSub()
            {
                let form = this.transferForm
                if (form.username == '')
                {
                    toast('请输入转账账号')
                    return false
                }
                if (form.money == '')
                {
                    toast('请输入转账金额')
                    return false
                }
                if (!validMoney(form.money))
                {
                    toast('请输入合法金额')
                    return false
                }
                if (Number(form.money) > Number(this.walletData.money))
                {
                    toast('余额不足')
                    return false
                }
                transfer(form).then(res => {
                    this.walletData.money = res.balance
                    Dialog.alert({
                        title: '系统提醒',
                        message: '转账成功'
                    }).then(() => {

                    });
                })
            }
        }
    }
</script>

<style scoped>
        @import "../../static/css/transfer.css";
</style>



